@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap);
body {
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
textarea:focus, input:focus, button, select, select:hover {
  outline: none;
}
button {
  font-family: 'Montserrat', sans-serif;
}
.nav {
    background: white;
    height: 80px;
    position: fixed;
    width: 100%;
    z-index: 3;
}
.nav__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}
.nav__logo, .nav__bag {
    margin: 0 50px;
    cursor: pointer;
}
.nav__logo {
    width: 40px;
    height: 40px;
}
.nav__links {
    display: flex;
}
.nav__links__element {
    margin: 0 20px;
}
.nav__links__element>a {
    color: black;
}
.nav__bag {
    display: flex;
    overflow: hidden;
    border-radius: 30px;
    height: 44px;
}
.nav__bag__icone{
    width: 20px;
    padding: 12px;
    background: #153774;
}

/* RESPONSIVE */
@media screen and (max-width: 650px) {
    .nav__logo {
        margin: 0 0 0 20px;
        width: 30px;
        height: 30px;
    }
    .nav__bag {
        margin: 0;
    }
    .nav__links {
        display: none;
    }
}
.Burger {
    display: none;
}

/* ICON ANIMATION */
.burger__icon {
    margin-right: 20px;
    height: 24px;
    width: 18px;
    cursor: pointer;
    position: relative;
}
.burger__icon>span {
    z-index: 1;
    height: 2px;
    width: 100%;
    display: block;
    position: absolute;
    top: 50%; transform: translateY(-50%);
    background-color: black;
    transition: all ease-in-out .3s;
}
.burger__icon>span::before, .burger__icon>span::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: black;
}
.burger__icon>span::before {
    transform: translateY(-5px);
    transition: all ease-in-out .3s;
}
.burger__icon>span::after {
    transform: translateY(5px);
    transition: all ease-in-out .3s;
}
.burger__icon.active>span{
    background-color: rgba(0, 0, 0, 0);
}

.burger__icon.active>span::before {
    transform: translateY(0px) rotateZ(45deg);
}
.burger__icon.active>span::after {
    transform: translateY(0px) rotateZ(-45deg);
}

/* BURGER ANIMATION */
.burger__anim {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    overflow: hidden;
    height: 100vh;
    top: 0;right: 0;
}
.burger__sidebar {
  position: absolute;
  background:white;
  opacity: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-left: 80em;
  overflow: hidden;
}
.burger__sidebar.open {
    opacity: 0;
    animation: fadeIn .5s forwards;
}
.burger__sidebar.close {
  opacity: 1;
  animation: fadeOut .5s forwards;
}

.burger__sidebar__items {
    padding: 0;
    text-align: center;
    margin-top: 100px;
}
.burger__sidebar__items>li {
    position: relative;
    margin: 30px 0;
}
.burger__sidebar__items>li>a {
    color: black;
}
/* KEYFRAMES */
@keyframes fadeIn {
    100% {opacity: 1;}
}
@keyframes fadeOut {
    100% {opacity: 0;}
}

@media screen and (max-width: 650px) {
    .Burger {
        display: block;
    }
}
