.product {
    padding-top: 3em;
    background: #C3E8ED;
}
.product__inner {
    padding: 15em 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.product__inner.cream {
    background: #F1EAE9;
}
.product__inner__img {
    width: 500px;
    height: 500px;
    margin-left: 350px;
}
.product__inner__box {
    margin-right: 350px;
    background: white;
    border: solid #E6E6E6 4px;
    padding: 40px 20px;
    width: 300px;
}
.product__inner__box__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.product__inner__box__top__txt>.title {
    font-size: 25px;
}
.product__inner__box__top__txt>.style {
    font-size: 15px;
}
.product__inner__box__top__price {
    color: rgb(117, 172, 34);
    font-weight: 500;
}
.product__inner__box__paragraph {
    margin-top: 50px;
}
.product__inner__box__number {
    position: relative;
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: solid #E6E6E6 1px;
}
.minus{
    cursor: pointer;
    height: 3px;
    width: 15px;
    background: #153774;
}
.more {
    height: 50px;
    width: 15px;
    cursor: pointer;
    position: relative;
}
.more>span {
    height: 3px;
    width: 100%;
    display: block;
    position: absolute;
    top: 50%; transform: translateY(-50%);
    background: #153774;
}
.more>span::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 3px;
    background: #153774;
    transform: rotate(90deg);
}
.product__inner__box__number>input {
    border: none;
    width: 20px;
    text-align: center;
}
.product__inner__box__cart {
    cursor: pointer;
    margin-top: 10px;
    border: 0;
    color: white;
    width: 100%;
    height: 50px;
    font-weight: 600;
    background: #153774;
    transition: all ease-in-out .2s;
}
.product__inner__box__cart:hover {
    background: #1e4ba0;
}

.product__add {
    position: fixed;
    display: flex;
    z-index: 10;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    font-weight: 500;
    font-size: 12px;
    padding: 20px;
    border-radius: 5px;
    top: 130px;
    white-space: pre;
}
.product__add>a {
    color: white;
    margin-left: 10px;
}
.product__add>a:hover {
    text-decoration: underline;
}
/* ADD ANIMATION */
.product__add.open {
    right: -500px;
    animation: open .5s forwards;
}
.product__add.close {
    right: 50px;
    animation: close .5s forwards;
}
/* KEYFRAMES */
@keyframes open {
    100% {right: 50px;}
}
@keyframes close {
    100% {right: -500px;}
}
@media screen and (max-width: 1800px) {
    .product__inner__img {
        margin-left: 250px;
    }
    .product__inner__box {
        margin-right: 250px;
    }
}
@media screen and (max-width: 1400px) {
    .product__inner__img {
        margin-left: 50px;
    }
    .product__inner__box {
        margin-right: 50px;
    }
}
@media screen and (max-width: 1000px) {
    .product__inner {
        flex-direction: column;
    }
    .product__inner__img {
        width: 400px;
        height: 400px;
        margin-left: 0px;
    }
    .product__inner__box {
        margin-top: 50px;
        margin-right: 0px;
    }
}
@media screen and (max-width: 650px) {
    .product__inner {
        padding: 10em 0;
    }
    .product__inner__img {
        width: 200px;
        height: 200px;
    }
    .product__inner__box {
        padding: 20px;
        width: 150px;
    }
    .product__inner__box__top {
        text-align: center;
        flex-direction: column;
    }
    .product__inner__box__top__txt>.title {
        font-size: 18px;
    }
    .product__inner__box__top__txt>.style {
        font-size: 12px;
    }
    .product__inner__box__top__price {
        font-size: 12px;
    }
    .product__inner__box__paragraph {
        margin-top: 10px;
        font-size: 10px;
    }
    .product__inner__box__number {
        position: relative;
        margin-top: 20px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        border: solid #E6E6E6 1px;
    }
    .more, .minus {
        width: 10px;
        height: 30px;
    }
    .more>span, .more>span::after, .minus {
        height: 2px;
    }
    .product__inner__box__number>input {
        border: none;
        width: 20px;
        text-align: center;
        font-size: 10px;
    }
    .product__inner__box__cart {
        height: 30px;
        font-size: 10px;
        font-weight: 400;
    }
    .product__add {
        margin: auto;
        padding: 10px 0;
        font-size: 8px;
        text-align: center;
    }
    /* ADD ANIMATION */
    .product__add.open,
    .product__add.close {
        right: 0;
        left: 0;
        width: 300px;
    }
    .product__add.open {
        opacity: 0;
        animation: open .5s forwards;
    }
    .product__add.close {
        animation: close .5s forwards;
    }
    /* KEYFRAMES */
    @keyframes open {
        100% {opacity: 1;}
    }
    @keyframes close {
        100% {right: -1300px;}
    }
}
@media screen and (max-width: 320px) {
    .product__add {
        display: none;
    }
}
.ShopCart>.background {
    position: absolute;
    background: #ECECEC;
    height: 100vh;
    width: 100%;
    z-index: -1;
}
.cart {
    padding: 8em 25em 3em;
    background: #ECECEC;
}
.cart__title {
    font-size: 40px;
}
.cart__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.cart__status {
    text-align: center;
    margin: 40px 0;
}
.cart__box {
    margin: auto;
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    width: 600px;
}
.cart__box__img {
    width: 150px;
    height: 150px;
}
.cart__box__right,
.cart__box__right__top,
.cart__box__right__bottom {
    display: flex;
}
.cart__box__right {
    flex-direction: column;
    width: 300px;
}
.cart__box__right__top {
    justify-content: space-between;
}
.cart__box__right__top__price {
    font-weight: 600;
}
.cart__box__right__bottom {
    margin-top: 80px;
    justify-content: space-between;
}
.cart__box__right__bottom>.number {
    border: solid white 2px;
    border-radius: 20px;
    text-align: center;
    background: #ECECEC;
    width: 80px;
    padding-left: 25px;
    height: 30px;
}
.cart__box__right__bottom>.number>option {
    text-align: center;
}
.cart__box__right__bottom>.remove {
    cursor: pointer;
}
.cart__inner__delete {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    text-align: center;
    cursor: pointer;
    color: white;
    background: black;
    width: 150px;
    height: 40px;
    font-size: 15px;
    border-radius: 5px;
    margin: 80px auto auto auto;
    transition: all ease-in-out .2s;
}
.cart__inner__delete:hover {
    background: rgb(37, 37, 37);
}
.cart__inner__cost,
.cart__inner__total {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 600px;
    margin: auto;
    margin-top: 50px;
}
.cart__inner__total {
    font-weight: 600;
}
.cart__inner__cost::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: black;
    margin-top: 40px;
}
.cart__checkout{
    border: none;
    cursor: pointer;
    font-weight: 600;
    font-size: 15px;
    background: #153774;
    color: white;
    border-radius: 50px;
    height: 50px;
    font-family: 'Montserrat', sans-serif;
    width: 500px;
    margin: 50px auto;
    transition: background ease-in-out .2s;
}
.cart__checkout:hover{
    background: #1d4a9c;
}
@media screen and (max-width: 1400px) {
    .cart {
        padding: 8em 0 3em;
    }
    .cart__title {
        text-align: center;
    }
}
@media screen and (max-width: 1000px) {
    .cart__title {
        font-size: 30px;
    }
    .cart__status {
        font-size: 20px;
    }
    .cart__box {
        width: 400px;
    }
    .cart__box__right__top__txt>.title,
    .cart__box__right__top__price {
        font-size: 15px;
    }
    .cart__box__right__top__txt>.paragraph {
        font-size: 12px;
    }
    .cart__box__right__bottom .number {
        font-size: 12px;
        width: 50px;
        height: 20px;
        padding-left: 12px;
    }
    .cart__box__right__bottom .remove {
        font-size: 12px;
    }
    .cart__inner__cost,
    .cart__inner__total {
        width: 400px;
    }
}
@media screen and (max-width: 650px) {
    .cart__title {
        font-size: 20px;
    }
    .cart__status {
        font-weight: 500;
        font-size: 15px;
    }
    .cart__inner * {
        margin: 0;
    }
    .cart__box,
    .cart__box__right {
        margin: auto;
        margin-top: 20px;
        width: 80%;
    }
    .cart__box__right__top__price{
        margin: 10px 0;
    }
    .cart__box,
    .cart__box__right,
    .cart__box__right__top,
    .cart__box__right__bottom {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    .cart__box__right__bottom .number {
        font-size: 12px;
        width: 50px;
        height: 20px;
        padding-left: 10px;
    }
    .cart__box__right__bottom .remove {
        margin: 20px 0;
    }
    .cart__inner__delete {
        font-size: 12px;
        margin: 30px auto;
    }
    .cart__inner__cost,
    .cart__inner__total {
        font-size: 12px;
        width: 80vw;
        margin: 20px auto auto auto;
    }
    .cart__inner__cost::after {
        margin-top: 23px;
        height: 1px;
    }
    .cart__checkout {
        font-weight: 500;
        font-size: 12px;
        width: 200px;
        height: 40px;
        margin: 40px auto 20px;
    }
}
